body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

@font-face {
  font-family: 'BBadr';
  src:        url(/static/media/BBadr.e8ae5adc.eot);
  src: local('\263A'),
  url(/static/media/BBadr.ae12f7af.woff) format('woff'),
  url(/static/media/BBadr.e3429be2.ttf) format('truetype'); 
  /* url('http://www.fontfarsi.ir/Content/Fonts/SVG/Far_Abasan.svg') format('svg'); */
  font-weight: normal;
  font-style: normal;
  }
  body{
    font-family: 'BBadr' !important;
    height: 100%;
  }
.App {
  text-align: center;
  direction: rtl;
  
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(2, 2, 2);
  background-image: url(/static/media/IMG_302122.f4e793d9.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  height: 150px;
  padding: 20px;
  color: white;
}
.root{
  width: 100%;
  margin-top: 30;
  overflow-x: 'auto';
}
.table {
  min-width: 700;
}
.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

