/* Drawer css */
.drawer-root {
  flex-Grow: 1;
  height: 730;
  z-Index: 1;
  overflow: 'hidden';
  position: 'relative';
  display: 'flex';
}
.drawer-appBar{
  z-Index: 10;
  /* z-Index: theme.zIndex.drawer + 1, */
  /* transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }), */
}
:root{
  --drawerWidth:240;
}
.drawer-appBarShift {
  
  margin-Left: 240;
  
  margin-Left: var(--drawerWidth);
  width: calc(100% - 240);
  width: calc(100% - var(--drawerWidth));
  /* width: calc(100% - var(--drawerWidth)px); */
  /* transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }), */
}
.drawer-menuButton {
  margin-Left: 12;
  margin-Right: 36;
}
.drawer-hide {
  display: 'none';
}
.drawer-drawerPaper {
  position: 'relative';
  white-Space: 'nowrap';
  width: 240;
  width: var(--drawerWidth);
  /* transition: theme.transitions.create('width', {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.enteringScreen,
  }), */
  transition: ease-in
  
}
.drawer-drawerPaperClose{
  overflow-X: 'hidden';
